import "./App.css";
import NavBar from "./components/NavBar";
import { useState, useEffect } from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import { default as EventsBrowser } from "./components/browser/Events/Events";
import { default as EventFormBrowser } from "./components/browser/Events/EventForm";
import { default as EventsMobile } from "./components/mobile/Events/Events";
import { default as EventFormMobile } from "./components/mobile/Events/EventForm";
import Test from "./components/Test";
import { default as DsaniBrowser } from "./components/browser/Dsani/Dsani";
import { default as DsaniMobile } from "./components/mobile/Dsani/Dsani";
import Footer from "./components/Footer";
import PrivacyPolicy from "./PrivacyPolicy";
// import Home from "./components/pages/Home";
import { BrowserView, MobileView } from "react-device-detect";

function App() {
  // let API_URL = "http://localhost:8000//api/";
  // API_URL = "https://stropdas2.herokuapp.com//api/";

  const [events, setEvents] = useState([]);
  const [leden, setLeden] = useState([]);
  useEffect(() => {
    const getEvent = async () => {
      let eventsFromServer = await fetchFromDatabase("/api/dsani/");
      setEvents(eventsFromServer);
    };

    const getLeden = async () => {
      var ledenFromServer = await fetchFromDatabase("/api/leden/");
      setLeden(ledenFromServer);
    };

    getLeden();
    getEvent();
  }, []);

  // const changePage = (page) => {
  //   setPaginator(page);
  // };
  // const maxPages = Math.ceil(events.length / numberDSANIEvents);
  // const CurrentPosts = events.slice(
  //   (paginator - 1) * numberDSANIEvents,
  //   paginator * numberDSANIEvents
  // );
  const fetchFromDatabase = async (route) => {
    console.log(route);
    const res = await fetch(route);
    const data = await res.json();
    return data;
  };

  // editing events
  const addEvent = async (event) => {
    const res = await fetch("/api/events/", {
      method: "POST",
      headers: { "Content-type": "application/json" },
      body: JSON.stringify(event),
    });
    const data = await res.json();
    setEvents([...events, data]);
  };

  const toggleEvent = async (event1) => {
    const res = await fetch(`/api/events/${event1.id}`, {
      method: "PUT",
      headers: {
        "Content-type": "application/json",
      },
      body: JSON.stringify(event1),
    });
    const data = await res.json();
    setEvents(events.map((event) => (event.id === event1.id ? data : event)));
  };
  // Delete events given an event
  const deleteEvent = async (event1) => {
    await fetch(`/api/events/${event1.id}`, {
      method: "DELETE",
      headers: {
        "Content-type": "application/json",
      },
    });
    // setEvents(events.filter((event) => event.id !== event1.id));
  };

  const editDsani = async (NI) => {
    const res = await fetch(`/api/dsani/${NI.id}`, {
      method: "PUT",
      headers: {
        "Content-type": "application/json",
      },
      body: JSON.stringify(NI),
    });
    const data = await res.json();
    setEvents(
      // console.log(
      events.map((event) =>
        event.dsani_ev.map((point) => point.id === NI.id)
          ? {
              ...event,
              dsani_ev: event.dsani_ev.map((point) =>
                point.id === NI.id ? data : point
              ),
            }
          : event
      )
    );
  };

  // const deleteDsani = async (NI) => {
  //   const res = await fetch(`/api/delete-dsani/${NI.id}`, {
  //     method: "DELETE",
  //     headers: {
  //       "Content-type": "application/json",
  //     },
  //   });
  //   const data = await res.json();
  //   console.log(data);
  //   setDsani(dsaniEvents.map((event) => event.id !== NI.id && event));
  // };
  return (
    <Router>
      <div className="cointainer">
        <NavBar />
        <BrowserView>
          <Routes>
            <Route
              path="/agenda"
              element={
                <>
                  <EventsBrowser
                    leden={leden}
                    allEvents={events}
                    onAdd={addEvent}
                    onEdit={toggleEvent}
                    onDelete={deleteEvent}
                  />
                </>
              }
            />
            <Route
              path="/dsani"
              element={
                <DsaniBrowser
                  allEvents={events}
                  onEdit={editDsani}
                  leden={leden}
                />
              }
            />
            <Route
              path="/addevent"
              element={<EventFormBrowser onAdd={addEvent} />}
            />
            <Route path="/test" element={<Test />} />
            <Route path="/privacy" element={<PrivacyPolicy />} />
          </Routes>
        </BrowserView>
        <MobileView>
          <Routes>
            <Route
              path="/agenda"
              element={
                <>
                  <EventsMobile
                    leden={leden}
                    allEvents={events}
                    onAdd={addEvent}
                    onEdit={toggleEvent}
                    onDelete={deleteEvent}
                  />
                </>
              }
            />
            <Route
              path="/dsani"
              element={
                <DsaniMobile
                  allEvents={events}
                  onEdit={editDsani}
                  leden={leden}
                />
              }
            />
            <Route
              path="/addevent"
              element={<EventFormMobile onAdd={addEvent} />}
            />
            {/* <Route path="/event" element={<Event event={events[0]} onAdd={addEvent} onEdit={toggleEvent} onDelete={deleteEvent}/>}/> */}
            <Route path="/test" element={<Test />} />
            <Route path="/privacy" element={<PrivacyPolicy />} />
          </Routes>
        </MobileView>
        <Footer />
      </div>
    </Router>
  );
}

export default App;
